<template>
    <main class="content container">
        <div class="content__top content__top--catalog">
            <h1 class="content__title">
                Каталог
            </h1>
            <span class="content__info">
                152 товара
            </span>
        </div>

        <div class="content__catalog">

            <ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo"
                :category-id.sync="filterCategoryId" :select-color.sync="filterColorId" />

            <section class="catalog">

                <div v-if="productsLoading">Загрузка товаров...</div>
                <div v-if="productsLoadingFailed">Произошла ошибка при загрузке товаров :( <button
                        @click="loadProducts">Попробовать ещё раз</button></div>

                <ProductList :products="products" />

                <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage" />

            </section>

        </div>
    </main>
</template>

<script>
import ProductList from '@/components/ProductList';
import BasePagination from '@/components/BasePagination';
import ProductFilter from '@/components/ProductFilter';
import axios from 'axios';
import { API_BASE_URL } from '@/config';
export default {
    components: { ProductList, BasePagination, ProductFilter },
    data() {
        return {
            filterPriceFrom: 0,
            filterPriceTo: 0,
            filterCategoryId: 0,
            filterColorId: 0,
            page: 1,
            productsPerPage: 3,
            colors: [],
            productsData: null,
            productsLoading: false,
            productsLoadingFailed: false,
            currentColor: '',
        }
    },
    computed: {

        products() {
            return this.productsData ?
                this.productsData.items.map(product => {
                    return {
                        ...product,
                        image: product.image.file.url
                    }
                })
                : [];
        },
        countProducts() {
            return this.productsData ? this.productsData.pagination.total : 0;
        },
    },
    methods: {
        submit() {
            this.$emit('update:priceFrom', this.currentPriceFrom);
            this.$emit('update:priceTo', this.currentPriceTo);
            this.$emit('update:categoryId', this.currentCategoryId);
            this.$emit('update:selectColor', this.currentColor)
        },
        reset() {
            this.$emit('update:priceFrom', 0);
            this.$emit('update:priceTo', 0);
            this.$emit('update:categoryId', 0);
            this.$emit('update:colorId', null);
        },
        loadProducts() {
            this.productsLoading = true;
            this.productsLoadingFailed = false;
            axios.get(API_BASE_URL + `/api/products`, {
                params: {
                    page: this.page,
                    limit: this.productsPerPage,
                    categoryId: this.filterCategoryId,
                    colorId: this.filterColorId,
                    minPrice: this.filterPriceFrom,
                    maxPrice: this.filterPriceTo
                }
            })
                .then(response => this.productsData = response.data)
                .catch(() => this.productsLoadingFailed = true)
                .then(() => this.productsLoading = false);
        },
    },
    watch: {
        page() {
            this.loadProducts();
        },
        filterPriceFrom() {
            this.loadProducts();
        },
        filterPriceTo() {
            this.loadProducts();
        },
        filterCategoryId() {
            this.loadProducts();
        },
        filterColorId() {
            this.loadProducts();
        },
        filterColorId() {
            this.loadProducts()
        },
    },
    created() {
        this.loadProducts();
    }
}
</script>