<template>
  <li class="catalog__item" @click.prevent="goToPage('product', { id: product.id })">
    <router-link class="catalog__pic" :to="{ name: 'product', params: { id: product.id } }">
      <img :src="product.image" :alt="product.title">
    </router-link>

    <h3 class="catalog__title">
      <a href="#">
        {{ product.title }}
      </a>
    </h3>

    <span class="catalog__price">
      {{ product.price | numberFormat }}
    </span>

    <ul class="colors colors--black">
      <li class="colors__item" v-for="colorData in product.colors" :key="colorData.id">
        <label class="colors__label">
          <input class="colors__radio sr-only" type="radio" :value="colorData.code" v-model="color">
          <span class="colors__value" :style="{ 'background-color': colorData.code }"></span>
        </label>
      </li>
    </ul>
    
  </li>
</template>

<script>
import goToPage from "@/helpers/goToPage"
import numberFormat from "@/helpers/numberFormat";
import axios from "axios";
export default {
  data() {
    return {
      color: '#73B6EA',
    };
  },
  props: ['product'],
  methods: {
    goToPage,
  },
  filters: {
    numberFormat
  },
}
</script>