<template>
    <div class="form__counter">
      <button @click="decrement" type="button" aria-label="Убрать один товар">
        <svg width="12" height="12" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>
  
      <input type="text" v-model.number="productAmount" @input="$emit('input', productAmount)">
  
      <button @click="increment" type="button" aria-label="Добавить один товар">
        <svg width="12" height="12" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>
  </template>
  
  <script>
  
  export default {
    data() {
        return {
            productAmount: 1
        }
    },
    props: {
      value: {
        type: Number,
        required: true
      }
    },
  
    methods: {
      increment() {
        this.productAmount++;
        this.$emit('input', this.productAmount);
      },
  
      decrement() {
        if (this.productAmount > 1) {
          this.productAmount--;
          this.$emit('input', this.productAmount);
        }
      },
      
    }
  };
  </script>
  